export const initialState = {
  isLogin: false,
  user: null,
};

export const authReducer = (state, action) => {
  switch (action.type) {
    case "LOGIN":
      return {
        ...state,
        isLogin: true,
        user: action.payload,
      };
    case "LOGOUT":
      return {
        ...state,
        isLogin: false,
        user: null,
      };
    case "GETUSER":
      return {
        ...state,
        isLogin: false,
        user: null,
      };
    case "UPDATE_USER":
      return {
        ...state,
        user: action.payload, // user   payload .
      };
    default:
      return state;
  }
};
